import { useTranslation } from "react-i18next";

import {
  HvVerticalNavigationHeader,
  HvVerticalNavigationTree,
  HvVerticalNavigationActions
} from "@hitachivantara/uikit-react-core";

import { useEffect, useRef } from "react";
import { useNavigation } from "../../../lib/hooks";
import { MenuItem } from "../../../lib/types";
import useNavigationContext from "../../../providers/hooks/useNavigationContext";
import { StyledHvVerticalNavigation, navigationPopupStyles } from "./styles";

import withClickAwayListener from "../../hoc/withClickAwayListener";
import useLayoutContext from "../../../providers/hooks/useLayoutContext";

const VerticalNavigation = () => {
  const { t } = useTranslation();
  const {
    selectedMenuItemId,
    rootMenuItemId,
    verticalNavigationItems,
    isCompactMode,
    verticalNavigationMode,
    switchVerticalNavigationMode
  } = useNavigationContext();
  const { setVerticalNavigationWidth } = useLayoutContext();
  const { navigate } = useNavigation();
  const verticalNavigationRef = useRef<HTMLDivElement>(null);
  const isVerticalNavigationExpanded = verticalNavigationMode === "EXPANDED";

  const changeHandler = (
    event: React.SyntheticEvent<Element, Event>,
    selectedItem: MenuItem
  ) => {
    // Due to the change from buttons to links on the navigation tree, we need to prevent the default behaviour of
    // the event to avoid full refreshes when clicking on the links.
    event.preventDefault();

    if (selectedItem.href) {
      navigate(selectedItem.href, {
        state: { selectedItemId: selectedItem.id }
      });

      if (isCompactMode) {
        switchVerticalNavigationMode();
      }
    }
  };

  const closePanelHandler = () => {
    switchVerticalNavigationMode();
  };

  useEffect(() => {
    const { current } = verticalNavigationRef;
    const firstChild = current?.childNodes[0] as HTMLElement;
    if (firstChild && !isCompactMode) {
      setVerticalNavigationWidth(firstChild.offsetWidth);
    } else {
      setVerticalNavigationWidth(0);
    }

    return () => setVerticalNavigationWidth(0);
  }, [verticalNavigationMode, isCompactMode, setVerticalNavigationWidth]);

  const collapseButtonAriaLabel = isVerticalNavigationExpanded
    ? t("verticalNavigation.ariaLabelCollapse")
    : t("verticalNavigation.ariaLabelExpand");

  return (
    <div ref={verticalNavigationRef}>
      <StyledHvVerticalNavigation
        isCompactMode={isCompactMode}
        open={isVerticalNavigationExpanded}
        useIcons
        slider={isCompactMode}>
        <HvVerticalNavigationHeader
          title={t("verticalNavigation.title") ?? ""}
          onCollapseButtonClick={!isCompactMode ? closePanelHandler : undefined}
          collapseButtonProps={{
            "aria-label": collapseButtonAriaLabel,
            "aria-expanded": isVerticalNavigationExpanded
          }}
          backButtonProps={{
            "aria-label":
              t("verticalNavigation.ariaLabelHeaderBackButton") ?? ""
          }}
        />

        <HvVerticalNavigationTree
          key={rootMenuItemId}
          mode="navigation"
          collapsible
          aria-label={t("verticalNavigation.ariaLabelNavigationTree") ?? ""}
          selected={selectedMenuItemId ?? ""}
          onChange={changeHandler}
          data={verticalNavigationItems}
          classes={{ navigationPopup: navigationPopupStyles }}
          sliderForwardButtonAriaLabel={
            t("verticalNavigation.ariaLabelSliderForwardButton") ?? ""
          }
        />

        <HvVerticalNavigationActions />
      </StyledHvVerticalNavigation>
    </div>
  );
};

export default withClickAwayListener(VerticalNavigation);
