import { useHvSnackbar } from "@hitachivantara/uikit-react-core";

import useBannerContext from "../../providers/hooks/useBannerContext";
import NOTIFICATION_TYPE, { NotificationDetail } from "../types/notifications";

const useNotificationsEventListener = () => {
  const { enqueueSnackbar } = useHvSnackbar();
  const { show } = useBannerContext();

  const handleCustomEventSnackbar = (notification: NotificationDetail) => {
    const snackbarContentProps = notification.actions
      ? {
          action: Array.isArray(notification.actions)
            ? notification.actions[0]
            : notification.actions,
          actionCallback: notification.actionsCallback
        }
      : undefined;

    if (notification.message) {
      enqueueSnackbar(notification.message, {
        variant: notification.variant || "default",
        snackbarContentProps
      });
    }
  };

  const handleCustomEventBanner = (notification: NotificationDetail) => {
    if (notification.message) {
      show(notification);
    }
  };

  const handleNotificationEvent = (event: CustomEvent) => {
    const type = event.detail?.type.toUpperCase();
    if (type === NOTIFICATION_TYPE.SNACKBAR) {
      handleCustomEventSnackbar(event.detail as NotificationDetail);
    } else if (type === NOTIFICATION_TYPE.BANNER) {
      handleCustomEventBanner(event.detail as NotificationDetail);
    } else {
      console.warn("Invalid notification type", type);
    }
  };

  return { handleNotificationEvent };
};

export default useNotificationsEventListener;
