import { useTranslation } from "react-i18next";

import { HvButton, HvHeaderActions } from "@hitachivantara/uikit-react-core";

import { HvAppShellHeaderAction } from "@hitachivantara/app-shell-shared";

import useAppShellConfig from "../../../../lib/hooks/useAppShellConfig";
import useNavigationContext from "../../../../providers/hooks/useNavigationContext";
import IconUiKit from "../../../IconUiKit";
import DynamicAction from "./DynamicAction";
import InternalAction, {
  internalActions
} from "./InternalActions/InternalAction/InternalAction";

const HeaderActions = () => {
  const { t } = useTranslation();
  const appShellConfig = useAppShellConfig();
  const {
    isCompactMode,
    switchVerticalNavigationMode,
    verticalNavigationMode,
    verticalNavigationItems
  } = useNavigationContext();

  const isVerticalNavigationClosed = verticalNavigationMode === "CLOSED";
  const showVerticalNavigationButton =
    isCompactMode && verticalNavigationItems.length > 0;

  return (
    <HvHeaderActions>
      {appShellConfig?.header?.actions.map(
        (action: HvAppShellHeaderAction, index: number) => {
          const headerActionKey = `headerAction_${index}`;
          if (
            !internalActions.find(
              internalAction => internalAction.bundle === action.bundle
            )
          ) {
            return (
              <DynamicAction
                key={headerActionKey}
                bundle={action.bundle}
                {...action.config}
              />
            );
          }
          return (
            <InternalAction
              key={headerActionKey}
              bundle={action.bundle}
              {...action.config}
            />
          );
        }
      )}

      {showVerticalNavigationButton && (
        <HvButton
          icon
          onClick={() => switchVerticalNavigationMode()}
          variant="secondaryGhost"
          aria-label={
            isVerticalNavigationClosed
              ? t("header.navigation.openNavigationPanel") ?? ""
              : t("header.navigation.closeNavigationPanel") ?? ""
          }
          aria-expanded={!isVerticalNavigationClosed}>
          <IconUiKit name={isVerticalNavigationClosed ? "Menu" : "Close"} />
        </HvButton>
      )}
    </HvHeaderActions>
  );
};

export default HeaderActions;
