import { useTheme } from "@hitachivantara/uikit-react-core";

import { ThemingDetails } from "../types/theming";
import useLocalStorage, { LOCAL_STORAGE_KEYS } from "./useLocalStorage";

const useThemeEventListener = () => {
  const { selectedTheme, changeTheme, selectedMode, colorModes } = useTheme();
  const { setStoredValue } = useLocalStorage(LOCAL_STORAGE_KEYS.COLOR_MODE);

  const getNextColorMode = () => {
    const index = colorModes.indexOf(selectedMode);

    const nextIndex = (index + 1) % colorModes.length;
    return colorModes[nextIndex];
  };

  const handleThemeEvent = (event: CustomEvent) => {
    const { colorMode } = event.detail as ThemingDetails;
    const newColorMode =
      colorModes.indexOf(colorMode) !== -1 ? colorMode : getNextColorMode();

    // For now we only want to change the colorMode
    changeTheme(selectedTheme, newColorMode);
    setStoredValue(newColorMode);
  };

  return { handleThemeEvent };
};

export default useThemeEventListener;
