import { Helmet } from "react-helmet-async";
import { useTranslation } from "react-i18next";

import {
  HvHeader,
  HvHeaderBrand,
  HvHeaderNavigation,
  HvHeaderNavigationItemProp
} from "@hitachivantara/uikit-react-core";

import BrandLogo from "./BrandLogo";
import StyledIconWrapper from "./styles";
import useAppShellConfig from "../../../lib/hooks/useAppShellConfig";
import useNavigation from "../../../lib/hooks/useNavigation";

import useNavigationContext from "../../../providers/hooks/useNavigationContext";

import { CONFIG_TRANSLATIONS_NAMESPACE } from "../../../lib/i18n";

import HeaderActions from "./HeaderActions";

const Header = () => {
  const { t: tConfig } = useTranslation(CONFIG_TRANSLATIONS_NAMESPACE);
  const appShellConfig = useAppShellConfig();
  const { items, selectedMenuItemId, rootMenuItemId, isCompactMode } =
    useNavigationContext();
  const { navigate } = useNavigation();

  const isOnlyTopMode = appShellConfig.navigationMode === "ONLY_TOP";
  const showNavigation =
    !isCompactMode &&
    appShellConfig.navigationMode !== "ONLY_LEFT" &&
    items.length > 0;

  const handleNavigationChange = (
    event: React.MouseEvent<Element, MouseEvent>,
    selectedItem: HvHeaderNavigationItemProp
  ) => {
    if (selectedItem.href) {
      navigate(selectedItem.href, {
        state: { selectedItemId: selectedItem.id }
      });
    }
  };

  const name = appShellConfig.name ? tConfig(appShellConfig.name) : "";

  return (
    <HvHeader position="fixed">
      <Helmet>
        <title>{name}</title>
      </Helmet>

      <HvHeaderBrand
        logo={
          <StyledIconWrapper>
            <BrandLogo logo={appShellConfig.logo} />
          </StyledIconWrapper>
        }
        name={name}
      />
      {showNavigation && (
        <HvHeaderNavigation
          data={items}
          selected={isOnlyTopMode ? selectedMenuItemId : rootMenuItemId}
          onClick={handleNavigationChange}
          levels={isOnlyTopMode ? 2 : 1}
        />
      )}

      <HeaderActions />
    </HvHeader>
  );
};

export default Header;
