import { useContext, useEffect, useMemo, useState } from "react";
import {
  HvAppShellConfig,
  HvAppShellContext,
  HvAppShellContextValue,
  HvAppShellRuntimeContext
} from "@hitachivantara/app-shell-shared";
import { BrowserRouter } from "react-router-dom";
import { I18nContext } from "react-i18next";

import {
  HvProvider,
  HvTheme,
  themes as baseThemes
} from "@hitachivantara/uikit-react-core";

import useAppShellConfig from "../../lib/hooks/useAppShellConfig";
import getBasePath from "../../lib/utils/basePathUtils";
import {
  addResourceBundles,
  CONFIG_TRANSLATIONS_NAMESPACE
} from "../../lib/i18n";
import useLocalStorage, {
  LOCAL_STORAGE_KEYS
} from "../../lib/hooks/useLocalStorage";

export type AppShellProviderProps = {
  children: React.ReactNode;
  config?: Partial<HvAppShellConfig>;
  configUrl?: string;
};

function mergeConfigs(
  baseConfig: HvAppShellConfig,
  ...patchConfigs: Partial<HvAppShellConfig>[]
): HvAppShellConfig {
  return patchConfigs.reduce((acc, patchConfig) => {
    if (patchConfig === undefined) {
      return acc;
    }

    if (acc === undefined) {
      return patchConfig;
    }

    return {
      ...acc,
      ...patchConfig
    };
  }, baseConfig) as HvAppShellConfig;
}

const AppShellProvider = ({
  children,
  config: localConfig,
  configUrl
}: AppShellProviderProps) => {
  const outerConfig = useAppShellConfig();
  const { i18n } = useContext(I18nContext);
  const { value: storedColorModeValue } = useLocalStorage(
    LOCAL_STORAGE_KEYS.COLOR_MODE
  );
  const [loadedConfig, setValue] = useState<HvAppShellConfig | undefined>(
    undefined
  );

  const [hasError, setHasError] = useState<boolean>(false);

  useEffect(() => {
    if (configUrl) {
      fetch(new URL(configUrl))
        .then(result => {
          return result.json();
        })
        .then(data => setValue(data))
        .catch(e => {
          console.error(
            `It was not possible to obtain the context from: ${configUrl}`,
            e
          );
          setValue(undefined);
          setHasError(true);
        });
    }
  }, [configUrl]);

  const tmpConfig: HvAppShellContextValue = useMemo(
    () =>
      mergeConfigs(
        loadedConfig as HvAppShellConfig,
        localConfig as HvAppShellConfig,
        outerConfig
      ),
    [loadedConfig, localConfig, outerConfig]
  );

  if (hasError) {
    throw Error("It was not possible to obtain the configuration");
  }

  const emptyConfig: HvAppShellConfig = {
    apps: [],
    menu: [],
    navigationMode: "TOP_AND_LEFT"
  };

  // as we are accepting localConfig as partial, some keys might be not passed and o merge with an empty config to guarantee that we have a complete config
  const value: HvAppShellContextValue = mergeConfigs(emptyConfig, tmpConfig);

  if (value.translations) {
    addResourceBundles(i18n, value.translations, CONFIG_TRANSLATIONS_NAMESPACE);
  }

  const [themes, setThemes] = useState<HvTheme[] | undefined>(undefined);

  useEffect(() => {
    if (value.theming?.themes) {
      Promise.all(
        value.theming.themes?.map(bundle => {
          return (
            ((bundle === "ds3" || bundle === "ds5") && baseThemes[bundle]) ||
            import(/* @vite-ignore */ bundle)
              .then(module => module.default)
              .catch(e => {
                console.error(`Import of theme bundle ${bundle} failed! ${e}`);
              })
          );
        })
      )
        .then(loadedThemes => {
          setThemes(loadedThemes.filter(theme => !!theme));
        })
        .catch(e => {
          console.error(`Import of themes failed! ${e}`);
        });
    }
  }, [value.theming?.themes]);

  const runtimeContext = useMemo(
    () => ({
      i18n
    }),
    [i18n]
  );
  if (!tmpConfig?.apps || (value.theming?.themes && !themes)) {
    return null;
  }

  return (
    <HvAppShellContext.Provider value={value}>
      <HvAppShellRuntimeContext.Provider value={runtimeContext}>
        <HvProvider
          themes={themes}
          theme={value.theming?.theme}
          colorMode={storedColorModeValue ?? value.theming?.colorMode}>
          <BrowserRouter basename={getBasePath(value)}>
            {children}
          </BrowserRouter>
        </HvProvider>
      </HvAppShellRuntimeContext.Provider>
    </HvAppShellContext.Provider>
  );
};

export default AppShellProvider;
