import { lazy, Suspense } from "react";
import { Route, Routes } from "react-router-dom";
import { ErrorBoundary } from "react-error-boundary";

import useAppShellConfig from "../../lib/hooks/useAppShellConfig";
import AppShellViewProvider from "../AppShellViewProvider";
import GenericError from "../../pages/GenericError";
import { getBundleUrl } from "../../lib/utils/navigationUtil";
import LoadingPage from "../../pages/LoadingPage";

const NotFound = lazy(() => import("../../pages/NotFound"));

const AppShellRoutes = () => {
  const config = useAppShellConfig();

  if (!config || !config.apps) {
    return null;
  }

  const views = config.apps.flatMap(
    app =>
      app.views?.map(view => ({
        id: app.id,
        bundle: getBundleUrl(app.id, view.bundle),
        route: view.route
      })) ?? []
  );

  return (
    <Suspense fallback={<LoadingPage />}>
      <Routes>
        {views.map(view => {
          const RouteComponent = lazy(
            () => import(/* @vite-ignore */ view.bundle)
          );

          return (
            <Route
              key={view.route}
              path={view.route}
              element={
                <AppShellViewProvider id={view.id}>
                  <ErrorBoundary
                    key={view.route}
                    fallback={<GenericError fullPage={false} />}>
                    <RouteComponent />
                  </ErrorBoundary>
                </AppShellViewProvider>
              }
            />
          );
        })}
        <Route path="*" key="notFound" element={<NotFound />} />
      </Routes>
    </Suspense>
  );
};

export default AppShellRoutes;
