import { createContext, useEffect, useMemo, useState } from "react";
import { css } from "@emotion/css";
import { v4 as uuid } from "uuid";

import { HvBanner, theme } from "@hitachivantara/uikit-react-core";
import { useTranslation } from "react-i18next";
import useNavigationContext from "./hooks/useNavigationContext";
import { SUB_HEADER_HEIGHT } from "../components/layout/Main/styles";
import { NotificationDetail } from "../lib/types/notifications";
import useLayoutContext from "./hooks/useLayoutContext";

export type BannerProviderProps = {
  children: React.ReactNode;
};

export interface Banner extends Omit<NotificationDetail, "type"> {
  id: string;
}

export interface BannerContextValue {
  show: (notification: NotificationDetail) => void;
  dismiss: (id: string) => void;
}

export const BannerContext = createContext<BannerContextValue>({
  show: () => {
    // Empty function
  },
  dismiss: () => {
    // Empty function
  }
});

const BannerProvider = ({ children }: BannerProviderProps) => {
  const { t } = useTranslation();
  const { showHeaderSubMenu, isCompactMode } = useNavigationContext();
  const { verticalNavigationWidth, setBannerMaxHeight } = useLayoutContext();
  const [banners, setBanners] = useState<Banner[]>([]);

  const show = (notification: NotificationDetail) => {
    const id = uuid();

    const newBanner: Banner = {
      id,
      ...notification,
      variant: notification.variant || "default"
    };
    setBanners(prevBanners => [...prevBanners, newBanner]);
  };

  const dismiss = (id: string) => {
    setBanners(prevBanners => prevBanners.filter(banner => banner.id !== id));
  };

  const value = useMemo(
    () => ({
      show,
      dismiss
    }),
    []
  );

  useEffect(() => {
    if (banners.length > 0) {
      // Get all heights from the rendered banners
      const bannerHeights = banners.map(item => {
        const bannerElement = document.getElementById(item.id);
        return bannerElement ? bannerElement.clientHeight : 0;
      });

      // Find the 'tallest' banner
      const maxBannerHeight = Math.max(...bannerHeights);

      // Update padding value
      setBannerMaxHeight(maxBannerHeight);
    } else {
      setBannerMaxHeight(0);
    }
  }, [banners, setBannerMaxHeight]);

  const styles = useMemo(
    () =>
      css({
        marginTop:
          showHeaderSubMenu && !isCompactMode
            ? `calc(${SUB_HEADER_HEIGHT}px + ${theme.space.sm})`
            : theme.space.sm,
        left: `calc(${verticalNavigationWidth}px + ${theme.space.sm})`,
        width: `calc(100% - (${verticalNavigationWidth}px + ${theme.space.sm}) - ${theme.space.sm})`,
        transform: "unset",
        minWidth: "unset",
        zIndex: theme.zIndices.banner
      }),
    [isCompactMode, showHeaderSubMenu, verticalNavigationWidth]
  );

  const closeBannerLabel = t("notifications.banner.close");

  return (
    <BannerContext.Provider value={value}>
      {banners.map(banner => (
        <HvBanner
          classes={{
            anchorOriginTopCenter: styles
          }}
          id={banner.id}
          key={banner.id}
          open
          onClose={() => dismiss(banner.id)}
          variant={banner.variant}
          label={banner.message}
          showIcon
          actions={banner.actions}
          actionsCallback={banner.actionsCallback}
          bannerContentProps={{
            actionProps: { "aria-label": closeBannerLabel }
          }}
        />
      ))}
      {children}
    </BannerContext.Provider>
  );
};

export default BannerProvider;
